/* COLORS */
:root {
  --background: #212121;
  --background-muted: #21212140;
  --foreground: #2b2b2b;
  --foreground-muted: #2b2b2b40;
  --text: #fafafa;
  --text-muted: #fafafa40;
  --primary: #ffe380;
  --primary-muted: #ffe38040;
  --secondary: #ffe380;
  --secondary-muted: #ffe38040;

  --dark: #00000040;
  --darker: #00000080;
  --light: #ffffff10;
  --lighter: #ffffff20;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  /* Percentage of user's browser font-size setting */
  font-size: 62.5%;
  scroll-behavior: smooth;
  font-family: 'Outfit', serif;
  background-color: var(--background);
}

body {
  color: var(--text);
  font-size: 1.4rem;
}

.drawer {
  width: 100%;
}

.drawer-body {
  margin-bottom: 1rem;
  background-color: var(--dark);
  padding: 2rem;
  border-radius: 5px;
}

pre {
  border: 1px dashed var(--secondary);
  color: var(--dark-text);
  page-break-inside: avoid;
  font-family: 'Inconsolata', monospace !important;
  font-size: 1.2rem;
  line-height: 1.6;
  overflow-x: hidden;
  padding: 1rem;
  display: block;
  white-space: pre;
  width: 100%;
}

li {
  list-style: none;
}

ul.badge-group {
  display: flex;
  flex-wrap: wrap;
  gap: 0.8rem;

  li {
    padding: 0.4rem 0.8rem;
    border-radius: 5px;
    background-color: var(--light);
  }
}

.capitalize {
  text-transform: capitalize;
}

.muted {
  opacity: 0.4;
}

.text-divider {
  margin: 0 1rem;
  opacity: 0.4;
}

.condition-icon {
  color: var(--text);
}

img.flag-sm {
  height: 1.2rem !important;
}

.btn {
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  gap: 0.4rem;

  font-size: inherit;
  font-family: inherit;

  color: var(--primary);
  text-decoration: none;

  padding-block: 0;
  border: none;
  background: none;

  border-bottom: 1px dashed var(--primary);
  transition: all 0.3s;

  i {
    font-size: 80%;
  }
}

.btn:hover {
  border-bottom: 1px dashed #00000000;
}

h1,
h2 {
  font-size: 1.4rem;
  font-weight: 100;
  text-transform: uppercase;
  letter-spacing: 4px;
}

h3 {
  border-bottom: 1px solid var(--lighter);
  padding-bottom: 0.4rem;
  margin-bottom: 0.8rem;
}

h1 {
  font-size: 3.2rem;
  letter-spacing: 8px;

  span {
    opacity: 0.4;
  }
}

.main {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

header {
  padding: 2rem;
  text-align: center;
}

article {
  position: relative;
  display: flex;
  height: 0;
  width: 96rem;
  flex-grow: 1;
  margin: 2rem auto;
}

section {
  height: 100%;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.wrap {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
}

section#list {
  width: 32rem;
  overflow-x: hidden;
  overflow-y: scroll;
}

section#details {
  width: 100%;
  overflow-x: hidden;
  overflow-y: scroll;
}

.castle-list {
  display: flex;
  flex-direction: column;
  gap: 0.4rem;
}

// SEARCH & FILTERS
.search {
  // display: flex;
  // flex-direction: column;
  gap: 1rem;
  // text-align: center;
  padding: 1rem 2rem;
}

// REUSABLES
.country-code {
  color: var(--background);
  background-color: #ffffff40;
  padding: 0.2rem 0.4rem;
  font-size: 1.2rem;
  border-radius: 5px;
}

// CASTLE LIST
.castle-tile {
  padding: 1rem 1.6rem;
  margin-right: 1rem;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 0.4rem;

  cursor: pointer;

  background-color: var(--light);
  transition: all 0.3s;

  h2.secondary {
    opacity: 0.4;
  }
}

.castle-tile:hover {
  background-color: var(--lighter);
}

.details {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 1.6rem;
  padding: 0.8rem;

  .info {
    border: 1px solid var(--foreground);
    padding: 2rem;

    display: flex;
    flex-direction: column;
    align-items: start;

    gap: 1rem;

    .info-header {
      display: flex;
      gap: 1rem;
      align-items: center;
      opacity: 0.4;
      margin-bottom: 1.2rem;
    }
  }

  // Locaiton
  .location {
    display: flex;
    gap: 4rem;

    .with-crest {
      display: flex;
      align-items: center;
      gap: 0.8rem;

      img {
        height: 3.2rem;
      }
    }

    .flag-sm {
      margin-right: 0.8rem;
    }
  }

  // Condition
  // .condition {
  // }

  // Dates
  // .dates {
  // }

  // Gallery
  .gallery {
    width: 100%;

    display: flex;
    gap: 1rem;
    // grid-template-columns: repeat(5, 1fr);

    .gallery-item {
      width: 4rem;
      height: 4rem;
      border-radius: 5px;

      overflow: hidden;
      background-color: var(--background);

      display: flex;
      flex-direction: column;
      position: relative;
      cursor: pointer;

      img {
        object-fit: cover;
        object-position: center;
        vertical-align: bottom;
        width: 100%;
        height: 100%;

        transition: all 0.3s;
      }

      .gallery-item-overlay {
        opacity: 0;
        position: absolute;
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        color: var(--text);
      }

      .gallery-item-overlay:hover {
        opacity: 1;
        background-color: #00000040;
      }
    }
  }

  // Footer
  .details-footer {
    display: flex;
    gap: 1rem;
    justify-content: center;
  }
}

.spotlight {
  width: 100%;

  img {
    height: 24rem;
    overflow: scroll;
    object-fit: cover;
    max-width: 100%;
  }
}

// FOOTER

footer {
  padding: 2rem;
  text-align: center;

  span {
    opacity: 0.4;
  }
}
